import { LoadingController, ModalController } from "ionic-angular";
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { JobLoopEventViewModel } from "../../Model/ViewModel/JobLoopEventViewModel";
import { GlobalComponent } from "../../components/global/global";
import { FilePathServicesProvider } from "../../providers/file-path-services/file-path-services";
import { FilePathViewModel } from "../../Model/ViewModel/FilePathViewModel";
import { ConnServicesProvider } from "../../providers/conn-services/conn-services";
/**
 * Generated class for the JobLoopEventModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-job-loop-event-add-edit-modal",
  templateUrl: "job-loop-event-add-edit-modal.html"
})
export class JobLoopEventAddEditModalPage {
  item: JobLoopEventViewModel;
  mode: string;
  change_mode: string;
  CanEditLoopJob: boolean;
  title: string;
  Job_type: string;
  default_path: FilePathViewModel[];
  src_file_path: FilePathViewModel;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController,
    private global: GlobalComponent,
    private FilePathServices: FilePathServicesProvider,
   private ConnServices: ConnServicesProvider
  ) {
    this.item = new JobLoopEventViewModel();
    this.item.loop_seq = navParams.data.item.loop_seq; //迴圈序號
    this.item.ssis_job_no = navParams.data.item.ssis_job_no; //SSIS作業編號
    this.item.job01 = navParams.data.item.job01; //作業迴圈欄位一
    this.item.job02 = navParams.data.item.job02; //作業迴圈欄位二
    this.item.job03 = navParams.data.item.job03; //作業迴圈欄位三
    this.item.event_type_key = navParams.data.item.event_type_key; //觸發事件類別主鍵
    this.item.src_path_key = navParams.data.item.src_path_key; //來源路徑主鍵
    this.item.file_keyword = navParams.data.item.file_keyword; //檔案搜尋關鍵字
    this.item.remote_conn_id = navParams.data.item.remote_conn_id; //遠端連線編號
    this.item.period_min = navParams.data.item.period_min; //每隔分鐘數
    this.item.active_start_date = navParams.data.item.active_start_date; //開始作業日期
    this.item.active_start_time = navParams.data.item.active_start_time; //開始作業時間
    this.item.next_start_time = navParams.data.item.next_start_time; //下次開始執行時間
    this.item.sbp_conn_id = navParams.data.item.sbp_conn_id; //sbpclient連線編號
    this.item.is_active = navParams.data.item.is_active; //啟用註記
    this.item.creator = navParams.data.item.creator; //鍵檔人員
    this.item.create_time = navParams.data.item.create_time; //建立時間
    this.item.modifier = navParams.data.item.modifier; //異動人員
    this.item.last_update_time = navParams.data.item.last_update_time; //最後更新時間
    this.item.event_type=navParams.data.item.event_type;
    this.item.src_path=navParams.data.item.src_path;
    this.item.remote_conn_string=navParams.data.item.remote_conn_string;
    this.item.sbp_conn_string=navParams.data.item.sbp_conn_string;
    this.CanEditLoopJob = navParams.data.CanEditLoopJob;
    this.mode = navParams.data.mode;
    this.change_mode = navParams.data.change_mode;
    this.title = this.item.ssis_job_no;


    if (this.mode === "POST") {
      this.get_src_path();
      this.get_remote_conn_string(this.item.remote_conn_id);
      this.get_sbp_conn_string(this.item.sbp_conn_id);
    }
  }

  SelectFtpConn() {
    let modal = this.modalCtrl.create("ConnSelectModalPage", {
      select_remote_conn_id: this.item.remote_conn_id,
      conn_type_key: "065|002"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.remote_conn_id = select_data.remote_conn_id;
      this.item.remote_conn_string = select_data.conn_string;
    });

    modal.present();
  }
  SelectSBPClientConn() {
    let modal = this.modalCtrl.create("ConnSelectModalPage", {
      select_remote_conn_id: this.item.remote_conn_id,
      conn_type_key: "065|005"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.sbp_conn_id = select_data.remote_conn_id;
      this.item.sbp_conn_string = select_data.conn_string;
    });

    modal.present();
  }
  SelectSrcPath() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.src_path_key,
      code_type: "005"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.src_path_key = select_data.code_key;
      this.item.src_path = select_data.code_desc;
    });

    modal.present();
  }
  get_src_path() {
    this.global.createLoader("取得預設資料夾中...");
    this.global.loading.present().then(() => {
      this.FilePathServices.GetDefaultListsByAsync("014|2").subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.default_path = data.Model;
            this.src_file_path = this.default_path.filter(
              (item, FilePathViewModel) => item.file_path_type_key === "004|SRC"
            )[0];
            this.item.src_path_key = this.src_file_path.file_path_key;
            this.item.src_path = this.src_file_path.file_path;

            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-" + err.message);
        }
      );
    });
  }
  get_remote_conn_string(remote_conn_id: string) {
    this.global.createLoader("取得連線字串中...");
    this.global.loading.present().then(() => {
      this.ConnServices.GetConnAsync(remote_conn_id).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.remote_conn_string = data.Model.conn_string;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-" + err.message);
        }
      );
    });
  }
  get_sbp_conn_string(sbp_conn_id: string) {
    this.global.createLoader("取得連線字串中...");
    this.global.loading.present().then(() => {
      this.ConnServices.GetConnAsync(sbp_conn_id).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.sbp_conn_string = data.Model.conn_string;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-" + err.message);
        }
      );
    });
  }
  SelectEventType() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.event_type_key,
      code_type: "078"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.event_type_key = select_data.code_key;
      this.item.event_type = select_data.code_desc;
      if(select_data.code_key=="078|HTTP")
      {
        this.item.period_min=5;
        this.item.file_keyword="";
      }
    });

    modal.present();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad JobLoopEventModalPage");
  }
}



